@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes slideUp {
  0% {
    opacity: 0;
    transform: translate3d(0, 100%, 0);
  }
  100% {
    opacity: 1;
    transform: none;
  }
}
@keyframes slideDown {
  0% {
    opacity: 0;
    transform: translate3d(0, -100%, 0);
  }
  100% {
    opacity: 1;
    transform: none;
  }
}
.contact {
  background: #111;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  height: 100%;
}
.contact .avatar {
  margin: auto;
  margin-top: -310px;
  margin-bottom: 30px;
  height: 280px;
  width: 280px;
  border-radius: 140px;
  background-clip: padding-box;
}
.contact .avatar.loaded,
.contact .no-js .avatar {
  background: #7f8c8d url("https://gravatar.com/avatar/21fc27a2ac6cd9094a423997f0344a0b?s=280") no-repeat;
  animation-name: fadeIn;
  animation-duration: 0.2s;
}
.contact .social a:hover {
  border-bottom: 1px solid white;
}
.contact .title {
  font-family: 'Open Sans';
  font-size: 32px;
  font-weight: 800;
  color: #dddddd;
  margin: 0;
}
.contact .subtitle {
  font-family: 'Source Sans Pro';
  font-size: 20px;
  font-weight: 300;
  margin-top: 17px;
  margin-bottom: 25px;
  color: #eeeeee;
  user-select: none;
}
.contact .subtitle .location {
  display: inline-block;
  font-family: 'Open Sans';
  font-weight: 800;
  font-size: 50%;
  color: #dddddd;
  vertical-align: top;
  transform: rotate(-10deg) translateY(-10px);
}
.contact .subtitle .location .striked {
  text-decoration: line-through;
  text-decoration-thickness: 3px;
  text-decoration-color: #a03535;
}
.contact .subtitle .location.invisible {
  color: #111;
}
.contact .title,
.contact .subtitle,
.contact .social {
  animation-name: slideDown;
  animation-duration: 0.2s;
}
.contact .social {
  animation-name: slideUp;
}
@media (max-height: 890px) {
  .contact .avatar.loaded,
  .no-js .contact .avatar {
    margin-top: -210px;
    height: 180px;
    width: 180px;
    border-radius: 90px;
    background-size: 180px;
  }
}
@media (max-height: 590px) {
  .contact .avatar.loaded,
  .no-js .contact .avatar {
    margin-top: -170px;
    height: 140px;
    width: 140px;
    border-radius: 70px;
    background-size: 140px;
  }
}
